import React from 'react'
import { Link } from 'gatsby'
import Flag from './Flag/Flag'
import TagList from './TagList'
import useSiteMetadata from '../hooks/use-site-config'
import styled from 'styled-components'
import { colors } from '../tokens'
import { Bull, ReadingTime } from './Commons'

import Img from 'gatsby-image'

const Post = styled.article`
  border-bottom: 1px solid rgba(214, 209, 230, 0.5);
  padding-bottom: 1.25rem;
`

const ReadPost = styled(Link)`
  display: block;
  font-size: 0.75rem;
  margin-top: 1rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  line-height: 2;
  color: ${colors.primary};

  &:hover {
    background-color: ${colors.primaryAlpha};
    border-radius: 0.25rem;
    color: ${colors.textLightest};
  }
`

const PostHeader = styled.header`
  padding: 1em 0;
`

const Excerpt = styled.p`
  line-height: 1.45;
  padding-bottom: 0.5em;
`

const PostTitleLink = styled(Link)`
  color: ${colors.primary};  
  &:hover {
    border-bottom: 1px dotted ${colors.primary};
  }
`

const FooterLine = styled.div`
  color: ${colors.textLight};
  font-size: 0.8em;
`


const PreviewCover = styled.div`
  width: auto;
  height: 200px;
  background: #c5d2d9 no-repeat 50%;
  background-size: cover;
  border-radius: 5px 5px 0 0;
`


const PostsListItem = props => {
  const { title, featuredImage, testFeature, excerpt, slug, language, tags, timeToRead } = props
  const { defaultLang } = useSiteMetadata()
  // const featuredImage = (featuredImage && featuredImage.publicURL) || fluid.src
  console.log("featuredImage is ", featuredImage)
  console.log("testFeature is ", testFeature)
  
  // console.log("featuredImage.publicURL is ", featuredImage.publicURL)
  
  return (
    <Post>
      <PostHeader>      
      {/* <Img sizes={featuredImage.childImageSharp.sizes} />         */}
        {/* <PreviewCover
          style={{ backgroundImage: `url("${featuredImage.publicURL}")` }} /> */}
        <h2>
          <PostTitleLink to={`/${slug}`} >
            {defaultLang !== language && <Flag language={language} />}
            {title}
          </PostTitleLink>
          
        </h2>
      </PostHeader>
      <section>
        <Excerpt dangerouslySetInnerHTML={{ __html: excerpt }} />
      </section>
      <footer>
        <FooterLine>
          <ReadingTime min={timeToRead} />
          <Bull />
          <TagList tags={tags} />
        </FooterLine>
        <ReadPost to={`/${slug}`} aria-label={`View ${title} article`}>
          Read post 
        </ReadPost>
      </footer>
    </Post>
  )
}
export default PostsListItem
